@import "~@ionic/angular/css/padding.css";
@import "~@ionic/angular/css/float-elements.css";
@import "~@ionic/angular/css/text-alignment.css";
@import "~@ionic/angular/css/text-transformation.css";
@import "~@ionic/angular/css/flex-utils.css";


ion-modal.modal-default:not(.overlay-hidden)~ion-modal.modal-default {
  --backdrop-opacity: 0.5;
  padding: 10px;
  background-color: #ededed;
}

* {
  font-family: Montserrat, ui-sans-serif, system-ui, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", Segoe UI Symbol, "Noto Color Emoji"
}


:root {
  --primary: #6C5CE7;
  --primary-light: #A29BFE;
  --primary-dark: #5649C0;
  --secondary: #00B894;
  --accent: #FD79A8;
  --dark: #2D3436;
  --light: #F5F6FA;
  --gray: #636E72;
  --light-gray: #DFE6E9;
  --white: #FFFFFF;
  --success: #00B894;
  --warning: #FDCB6E;
  --danger: #D63031;
  --info: #0984E3;

  --border-radius: 16px;
  --border-radius-sm: 8px;
  --shadow-sm: 0 2px 8px rgba(0, 0, 0, 0.08);
  --shadow-md: 0 4px 12px rgba(0, 0, 0, 0.12);
  --shadow-lg: 0 8px 24px rgba(0, 0, 0, 0.16);
  --transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
}

$primary: #6C5CE7;
$primary-light: #A29BFE;
$success: #00B894;
$danger: #D63031;
$text: #2D3436;
$text-light: #636E72;
$bg: #F5F6FA;
$card-bg: #FFFFFF;
$shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
$neumorph: 8px 8px 15px rgba(0, 0, 0, 0.05),
  -8px -8px 15px rgba(255, 255, 255, 0.8);

.phone-section {
  overflow: auto;
  display: flex;
  flex-direction: column;
  position: relative;
  background-color: #ededed;
}

.homeHeader {
  padding: 15px 24px 20px;
  position: relative;
  z-index: 1;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
  background: #ededed;
  width: 100%;
  z-index: 200;
}

.homeHeader-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.user-greeting h1 {
  font-size: 22px;
  font-weight: 600;
  margin-bottom: 6px;
  word-break: break-word;
}

.user-greeting p {
  font-size: 14px;
  opacity: 0.9;
  font-weight: 300;
  margin: 0;
}

.user-avatar {
  width: 52px;
  height: 52px;
  margin-left: 5px;
  border-radius: 16px;
  object-fit: cover;
}

.stats-container {
  display: flex;
  gap: 16px;
  margin-top: 24px;
}

.stat-card {
  flex: 1;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(5px);
  border-radius: var(--border-radius-sm);
  padding: 12px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.stat-card i {
  font-size: 18px;
  margin-bottom: 6px;
}

.stat-card .value {
  font-size: 18px;
  font-weight: 600;
}

.stat-card .label {
  font-size: 12px;
  opacity: 0.8;
}

.skeleton {
  position: relative;
  height: 93px;
  overflow: hidden;
}

.skeleton::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(90deg,
      transparent,
      rgba(255, 255, 255, 0.1),
      transparent);
}


.section {
  padding: 24px;
  flex: 1;
  overflow-y: auto;
  background: #ededed;
  margin-bottom: 50px;
}

.section-title {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 20px;
  color: var(--dark);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.section-title a {
  font-size: 14px;
  font-weight: 500;
  color: var(--dark);
  text-decoration: none;
  display: flex;
  align-items: center;
  gap: 4px;
}

.search-bar {
  position: relative;
  margin-bottom: 15px;
}

.search-bar .fa-search{
  position: absolute;
  left: 18px;
  top: 50%;
  transform: translateY(-50%);
  color: var(--gray);
}

.search-bar .fa-close{
  position: absolute;
  right: 18px;
  top: 50%;
  transform: translateY(-50%);
  color: var(--gray);
}

.search-bar input {
  width: 100%;
  padding: 16px 20px 16px 50px;
  border-radius: 15px;
  border: none;
  font-size: 15px;
}

.search-bar input:focus {
  outline: none;
}

.category-scroll {
  display: flex;
  overflow-x: auto;
  padding-bottom: 8px;
  scrollbar-width: none;
}


.category-scroll::-webkit-scrollbar {
  display: none;
}

.category-card {
  min-width: 90px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 15px;
  transition: var(--transition);
  cursor: pointer;
  flex-shrink: 0;
}
.category-card:nth-child(1){
  min-width: unset !important;
  margin-right: 15px;
}

.category-card.active {
  color: var(--primary);
}


.icon-wrapper {
  width: 44px;
  height: 44px;
  border-radius: 14px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  background: var(--white);
}

.category-card.active .icon-wrapper {
  background: var(--white);
  color: var(--primary);
}

.category-card h4 {
  font-size: 13px;
  font-weight: 500;
}

.quiz-card {
  background-color: $card-bg;
  border-radius: 16px;
  padding: 15px;
  margin-bottom: 14px;
  transition: all 0.3s;
  position: relative;
  overflow: hidden;
}



.quiz-card .banner {
  width: 100%;
  height: 120px;
  object-fit: cover;
  position: relative;
}

.quiz-card .banner::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 60px;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.7), transparent);
}

.quiz-card .badge {
  position: absolute;
  top: 12px;
  left: 12px;
  background: var(--accent);
  color: var(--white);
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 600;
  z-index: 1;
}

.quiz-card .content {
  padding: 16px;
}

.quiz-card h3 {
  font-size: 16px;
  color: var(--dark);
  margin: unset;
}

.quiz-card .meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 13px;
  color: var(--gray);
}

.quiz-card .participants {
  display: flex;
  align-items: center;
}

.quiz-card .participants img {
  width: 22px;
  height: 22px;
  border-radius: 50%;
  margin-right: -8px;
}

.quiz-card .participants span {
  margin-left: 12px;
  font-size: 12px;
}

.quiz-card .points {
  color: var(--primary);
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 4px;
}


.progress-homeHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
}

.progress-title {
  font-size: 16px;
  font-weight: 600;
  color: var(--dark);
  display: flex;
  align-items: center;
  gap: 8px;
}

.progress-title i {
  color: var(--primary);
}

.progress-percent {
  font-weight: 600;
  color: var(--primary);
}

.progress-bar {
  height: 8px;
  background: var(--light-gray);
  border-radius: 10px;
  margin-bottom: 12px;
  overflow: hidden;
}

.progress-fill {
  height: 100%;
  width: 65%;
  background: linear-gradient(90deg, var(--primary), var(--primary-light));
  border-radius: 10px;
  position: relative;
  transition: width 1s ease-in-out;
}

.progress-fill::after {
  content: '';
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  width: 20px;
  background: linear-gradient(to right, transparent, rgba(255, 255, 255, 0.5));
}

.progress-stats {
  display: flex;
  justify-content: space-between;
  font-size: 13px;
  color: var(--gray);
}

.leaderboard-item {
  display: flex;
  align-items: center;
  padding: 10px 5px;
}

.leaderboard-item:last-child {
  border-bottom: none;
}

.rank {
  font-weight: 700;
  color: var(--gray);
  width: 28px;
  text-align: center;
  font-size: 15px;
}

.leaderboard-item .user {
  display: flex;
  align-items: center;
  flex: 1;
  margin-left: 12px;
}

.leaderboard-item img {
  width: 36px;
  height: 36px;
  border-radius: 12px;
  object-fit: cover;
  margin-right: 12px;
}

.leaderboard-item .name {
  font-size: 15px;
  font-weight: 500;
}

.leaderboard-item .level {
  font-size: 12px;
  color: var(--gray);
  margin-top: 2px;
}

.leaderboard-item .points {
  font-weight: 700;
  color: var(--primary);
  font-size: 15px;
  margin-left: 21px;
  margin-right: 17px;

}

.bottom-nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 14px 0;
  background: var(--white);
  position: absolute;
  width: 100%;
  bottom: 0;
  z-index: 10;
}

.nav-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: var(--gray);
  text-decoration: none;
  font-size: 12px;
  position: relative;
  transition: var(--transition);
}

.nav-item i {
  font-size: 22px;
  margin-bottom: 4px;
  transition: var(--transition);
}

.nav-item.active {
  color: var(--primary);
}


.nav-item .badge {
  position: absolute;
  top: -4px;
  right: -4px;
  background: var(--danger);
  color: var(--white);
  width: 18px;
  height: 18px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 10px;
  font-weight: bold;
}

.floating-btn {
  position: absolute;
  bottom: 80px;
  right: 24px;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: linear-gradient(135deg, var(--accent), #E84393);
  color: var(--white);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 22px;
  box-shadow: 0 6px 20px rgba(253, 121, 168, 0.4);
  z-index: 100;
  transition: var(--transition);
  border: none;
  cursor: pointer;
}

.floating-btn:hover {
  transform: translateY(-4px) scale(1.05);
  box-shadow: 0 8px 24px rgba(253, 121, 168, 0.5);
}

/* Animation for quiz cards */

.quiz-card:nth-child(1) {
  animation-delay: 0.1s;
}

.quiz-card:nth-child(2) {
  animation-delay: 0.2s;
}

.quiz-card:nth-child(3) {
  animation-delay: 0.3s;
}

.quiz-card:nth-child(4) {
  animation-delay: 0.4s;
}

/* Pulse animation for FAB */
@keyframes pulse {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.05);
  }

  100% {
    transform: scale(1);
  }
}

.floating-btn {
  animation: pulse 2s infinite;
}



/* Verification Modals */
.verification-container {
  text-align: center;
  padding: 40px 24px;
  background: white;
}

.loader {
  position: relative;
  width: 80px;
  height: 80px;
  margin: 0 auto 24px;
}

.loader-spinner {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 3px solid rgba(108, 92, 231, 0.1);
  border-top: 3px solid #6c5ce7;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

.loader-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.success-animation {
  width: 100px;
  height: 100px;
  margin: 0 auto 24px;
}

.checkmark {
  width: 100px;
  height: 100px;
}

.checkmark__circle {
  stroke-dasharray: 166;
  stroke-dashoffset: 166;
  stroke-width: 2;
  stroke-miterlimit: 10;
  stroke: #6c5ce7;
  fill: none;
  animation: stroke 0.6s cubic-bezier(0.65, 0, 0.45, 1) forwards;
}

.checkmark__check {
  transform-origin: 50% 50%;
  stroke-dasharray: 48;
  stroke-dashoffset: 48;
  stroke-width: 3;
  stroke: #6c5ce7;
  animation: stroke 0.3s cubic-bezier(0.65, 0, 0.45, 1) 0.8s forwards;
}

.failure-animation {
  width: 100px;
  height: 100px;
  margin: 0 auto 24px;
}

.crossmark {
  width: 100px;
  height: 100px;
}

.crossmark__circle {
  stroke-dasharray: 166;
  stroke-dashoffset: 166;
  stroke-width: 2;
  stroke-miterlimit: 10;
  stroke: #ff5252;
  fill: none;
  animation: stroke 0.6s cubic-bezier(0.65, 0, 0.45, 1) forwards;
}

.crossmark__cross {
  transform-origin: 50% 50%;
  stroke-dasharray: 48;
  stroke-dashoffset: 48;
  stroke-width: 3;
  stroke: #ff5252;
  animation: stroke 0.3s cubic-bezier(0.65, 0, 0.45, 1) 0.8s forwards;
}

.failure-message {
  color: #ff5252;
  text-align: center;
  font-weight: 500;
  margin-top: 16px;
}

/* Reuse the same stroke animation from success */
@keyframes stroke {
  100% {
    stroke-dashoffset: 0;
  }
}

.verification-text h3 {
  color: #2d3436;
  font-size: 20px;
  margin-bottom: 8px;
}

.verification-text .main .error {
  width: 100%;
  text-align: left;
}

.verification-text .main {
  width: 100%;
  display: grid;
  justify-content: center;
}

.verification-text p {
  color: #636e72;
  font-size: 14px;
  margin: 0;
}



@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes stroke {
  100% {
    stroke-dashoffset: 0;
  }
}

@keyframes scale {

  0%,
  100% {
    transform: none;
  }

  50% {
    transform: scale3d(1.1, 1.1, 1);
  }
}

@keyframes fill {
  100% {
    box-shadow: inset 0 0 0 100px #6c5ce7;
  }
}


.otp-container {
  background-color: white;
  border-radius: 16px;
  padding: 40px;
  text-align: center;
}

.logo {
  margin-bottom: 24px;
}



.subtitle {
  color: #666;
  font-size: 14px;
  margin-bottom: 32px;
}

.otp-inputs {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-bottom: 32px;
}




.verify-btn {
  background-color: #4a90e2;
  color: white;
  border: none;
  border-radius: 8px;
  padding: 14px;
  width: 100%;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.3s;
}

.verify-btn:hover {
  background-color: #3a7bc8;
}

.resend-text {
  margin-top: 24px;
  font-size: 14px;
  color: #666;
}

.resend-link {
  color: #4a90e2;
  text-decoration: none;
  font-weight: 500;
}

.otp-input {
  width: 100%;
  height: 50px;
  text-align: center;
  font-size: 24px;
  border: 1px solid #ddd;
  border-radius: 8px;
  outline: none;
  transition: all 0.3s;
}

.otp-input:focus {
  border-color: #4a90e2;
}

.autogenerated-quizzes {
  margin-bottom: 60px;
}

.section-title {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 16px;
  color: $text;
  display: flex;
  align-items: center;
  gap: 8px;
}

.section-title svg {
  width: 20px;
  height: 20px;
}

.quiz-title {
  font-size: 16px;
  font-weight: 700;
  color: $text;
  display: flex;
  align-items: center;
}

.quiz-title svg {
  width: 18px;
  height: 18px;
  color: $primary;
}

.quiz-description {
  font-size: 14px;
  color: $text-light;
  line-height: 1.5;
  margin: unset;
  margin-top: 5px;
}

.quiz-header .quiz-meta {
  margin: unset;
}

.quiz-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 12px;
  color: $text-light;
  margin-bottom: 16px;
}

.meta-item {
  display: flex;
  align-items: center;
  gap: 4px;
}

.meta-item svg {
  width: 14px;
  height: 14px;
}

.quiz-actions {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
}

.action-btn {
  padding: 10px 18px;
  border-radius: 10px;
  font-weight: 600;
  cursor: pointer;
  border: none;
  transition: all 0.2s;
  font-size: 14px;
  display: flex;
  gap: 5px;
  align-items: center;
}

.approve-btn {
  background-color: #346a35;
  color: #fff;
}

.delete-btn {
  background-color: #ef1563;
  color: white;
}

.empty-state {
  text-align: center;
  padding: 40px 20px;
  color: $text-light;
  font-size: 15px;
  background-color: $card-bg;
  border-radius: 16px;
  margin-top: 20px;

  ion-icon {
    font-size: 30px;
  }
}




.ph-item {
  padding: unset !important;
  margin: unset !important;
  border: unset !important;
  border-radius: 16px !important;
  background: unset !important;
}

.ph-item>* {
  margin-top: 15px !important;
}

.ph-avatar {
  width: 35px;
  height: 35px;
  min-width: unset;
  margin: unset;
}

.ph-row div {
  height: unset;
}

.floating-action {
  position: fixed;
  bottom: 170px;
  right: 30px;
  width: 56px;
  height: 56px;
  background-color: $primary;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.3s;
  z-index: 10;
}

.floating-action:hover {
  transform: scale(1.1);
}

.floating-action svg {
  width: 24px;
  height: 24px;
  color: white;
}

@media (max-width: 480px) {
  .phone-section {
    padding: 16px;
  }

  .input-container {
    padding: 16px;
  }

  .big-input {
    min-height: 140px;
    padding: 14px;
  }


}

.loading {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}